/* CSS ÏïÑÏù¥ÏΩò */
/* Î°úÍ∑∏Ïù∏ */
i {
  float: left;
  margin: 0 10px 0 5px;
}
.logIn {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 8px;
  height: 16px;
  border: 1px solid;
  border-left: 0;  
}
.logIn::after, .logIn::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
}
.logIn::after {
  border-top: 2px solid;
  border-right: 2px solid;
  border-radius: 2px;
  transform: rotate(45deg);
  width: 7px;
  height: 7px;
  left: -3px;
  top: 3px;
}
.logIn::before {
  width: 10px;
  height: 1px;
  background: currentColor;
  left: -8px;
  top: 6px;
}

/* ÌöåÏõêÍ∞ÄÏûÖ */
.userAdd {
  display: block;
  transform: scale(1);
  box-sizing: border-box;
  width: 20px;
  height: 18px;
  background: linear-gradient(to left, currentColor 8px, transparent 0) no-repeat 14px 6px/6px 2px, linear-gradient(to left, currentColor 8px, transparent 0) no-repeat 16px 4px/2px 6px;
}
.userAdd::after, .userAdd::before {
  content: "";
  display: block;
  box-sizing: border-box;
  position: absolute;
  border: 1px solid;
}
.userAdd::before {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  top: 0;
  left: 4.5px;
}
.userAdd::after {
  width: 16px;
  height: 7px;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  top: 9px;
}

/* Ìë∏ÌÑ∞Ï≤òÎ¶¨ */
.layoutwrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.mainContent {
  flex: 1;
  padding: 20px;
}